<template>
  <div id="all">
    <h1>All Book</h1>
    <hr />
    <h3 v-if="loading" class="display-4 mt-5">Please wait a seconds...</h3>
    <div class="content" v-if="loading">
      <SkeletonCard v-for="n in 6" :key="n" />
    </div>
    <div class="content">
      <CardBook v-for="(book,index) in books" :key="index" :book="book"></CardBook>
    </div>
  </div>
</template>

<script>
import { QUERY_BOOKS } from "@/graphql/query";
export default {
  name: "bookAll",
  components: {
    CardBook: () => import("../CardBook"),
    SkeletonCard: () => import("../SkeletonLoad"),
  },
  data() {
    return {
      books: [],
      loading: false,
    };
  },
  async mounted() {
    try {
      this.loading = true;
      const {
        data: { books },
      } = await this.$apollo.query({
        query: QUERY_BOOKS,
      });
      setTimeout(() => (this.loading = false), 1000);
      return (this.books = books);
    } catch (error) {
      console.log(error);
    }
  },
  methods: {},
};
</script>

<style lang="scss" scoped>
@import "~@/assets/scss/_variables.scss";

#all {
  padding: 3rem;

  h1 {
    font-size: $title-md;
  }

  hr {
    border: $color-border-gray;
  }

  .content {
    margin-top: 3rem;
    width: 100%;
    min-height: 20vh;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 0.68fr));
    grid-template-rows: 1fr;
    grid-gap: 3.1rem;
    justify-content: center;

    .card-book {
      transition: all 0.6s ease;
      transform: scale(1);
      cursor: pointer;
    }

    .card-book:hover {
      transform: scale(1.075);
    }
  }
}
</style>